<template>
  <div>
    <category-nav></category-nav>
    <category-book-list @add-to-cart="emitAddedCart"> </category-book-list>
  </div>
</template>

<script>
import CategoryNav from "@/components/CategoryNav";
import CategoryBookList from "@/components/CategoryBookList";

export default {
  name: "categoryView",
  components: {
    CategoryNav,
    CategoryBookList,
  },
  methods: {
    emitAddedCart: function (book) {
      this.$emit("add-to-cart", book);
    },
  },
  data: function () {
    return {};
  },
  created: function () {
    const self = this;
    this.$store.dispatch("selectCategory", this.$route.params.name);

    this.$store.dispatch("fetchSelectedCategoryBooks").catch(function () {
      self.$router.push("/404");
    });
  },
};
</script>

