<template>
  <div>
    <section v-if="cart.numberOfItems == 0" class="categoryView">
      <div>
        <h2>No items in your cart</h2>
        <cart-view-buttons></cart-view-buttons>
      </div>
    </section>
    <section class="categoryView" v-if="!cart.empty">
      <form @submit.prevent="submitOrder">
        <div>
          <div>
            <label for="name">Name</label>
            <input
              type="text"
              size="20"
              id="name"
              name="name"
              v-model.lazy="$v.name.$model"
            />
          </div>
          <template v-if="$v.name.$error">
            <span class="error" v-if="!$v.name.required">Name is required</span>
            <span class="error" v-else-if="!$v.name.minLength">
              Name must have at least
              {{ $v.name.$params.minLength.min }} letters.
            </span>
            <span class="error" v-else-if="!$v.name.maxLength">
              Name can have at most
              {{ $v.name.$params.maxLength.max }} letters.
            </span>
            <span class="error" v-else> An unexpected error occurred. </span>
          </template>
        </div>
        <!-- TODO: Add address input and validation messages -->

        <div>
          <div>
            <label>Address</label>
            <input
              type="text"
              size="20"
              id="address"
              name="address"
              v-model.lazy="$v.address.$model"
            />
          </div>

          <template v-if="$v.address.$error">
            <span class="error" v-if="!$v.address.required">
              Address is required</span
            >
            <span class="error" v-else-if="!$v.address.minLength">
              Address must have at least
              {{ $v.address.$params.minLength.min }} letters.
            </span>
            <span class="error" v-else-if="!$v.address.maxLength">
              Address can have at most
              {{ $v.address.$params.maxLength.max }} letters.
            </span>
            <span class="error" v-else> An unexpected error occurred. </span>
          </template>
        </div>
        <div>
          <div>
            <label for="phoneNumber">Phone</label>
            <input
              class="textField"
              type="text"
              size="20"
              id="phoneNumber"
              name="phoneNumber"
              v-model.lazy="$v.phoneNumber.$model"
            />
          </div>
          <template v-if="$v.phoneNumber.$error">
            <span class="error" v-if="!$v.phoneNumber.required">
              Phone is required</span
            >
            <span class="error" v-else-if="!$v.phoneNumber.phone">
              Invalid phone number</span
            >
            <span class="error" v-else> An unexpected error occurred. </span>
          </template>
        </div>
        <div>
          <div>
            <label for="email">Email</label>
            <input
              type="text"
              size="20"
              id="email"
              name="email"
              v-model.lazy="$v.email.$model"
            />
          </div>
          <template v-if="$v.email.$error">
            <span class="error" v-if="!$v.email.required">
              Email is required</span
            >
            <span class="error" v-else-if="$v.email.required && !$v.email.email"
              >Invalid Email</span
            >
            <span class="error" v-else> An unexpected error occurred. </span>
          </template>
        </div>

        <div>
          <div>
            <label for="ccNumber">Credit card</label>
            <input
              type="text"
              size="20"
              id="ccNumber"
              name="ccNumber"
              v-model.lazy="$v.ccNumber.$model"
            />
          </div>
          <template v-if="$v.ccNumber.$error">
            <span class="error" v-if="!$v.ccNumber.required">
              Credit card number is required</span
            >
            <span class="error" v-else-if="!$v.ccNumber.creditCard"
              >Invalid credit card number</span
            >
            <span class="error" v-else> An unexpected error occurred. </span>
          </template>
        </div>

        <div>
          <div>
            <label>Exp date</label>
            <select v-model="ccExpiryMonth">
              <option
                v-for="(month, index) in months"
                :key="index"
                :value="index + 1"
              >
                {{ month }} ({{ index + 1 }})
              </option>
            </select>
            <select v-model="ccExpiryYear">
              <option
                v-for="i in 15"
                :value="i - 1 + yearFrom(i - 1)"
                :key="i - 1"
              >
                {{ yearFrom(i - 1) }}
              </option>
            </select>
          </div>
        </div>
        <div>
          <input
            id="complete-purchase-btn"
            type="submit"
            name="submit"
            :disabled="checkoutStatus === 'PENDING'"
            :class="{ 'disabled-btn': checkoutStatus === 'PENDING' }"
            value="Complete Purchase"
          />
        </div>
      </form>
      <div class="payment-info">
        <div>
          You cart total is :
          <b>${{ ($store.state.cart.total / 100).toFixed(2) }}</b>
        </div>
        <div>
          You cart subtotal is :
          <b>${{ ($store.state.cart.subtotal / 100).toFixed(2) }}</b>
        </div>
        <div>
          You surcharge is :
          <b>${{ ($store.state.cart.surcharge / 100).toFixed(2) }}</b>
        </div>
      </div>
      <section v-show="checkoutStatus !== ''" class="checkoutStatusBox">
        <div v-if="checkoutStatus === 'ERROR'">
          <i class="fa-solid fa-xmark cross-icon"></i> Error: Please fix the
          problems above and try again.
        </div>

        <div v-else-if="checkoutStatus === 'PENDING'">
          <i class="fa-solid fa-spinner"></i> Processing...
        </div>

        <div v-else-if="checkoutStatus === 'OK'">
          <i class="fa-solid fa-thumbs-up"></i> Order placed...
        </div>

        <div v-else>
          <!-- "checkoutStatus == 'SERVER_ERROR'" -->
          An unexpected error occurred, please try again.
        </div>
      </section>
    </section>
  </div>
</template>

<script>
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";
import CartViewButtons from "@/components/CartViewButtons";

import isCreditCard from "validator/lib/isCreditCard";
import isMobilePhone from "validator/lib/isMobilePhone";

const phone = (value) => isMobilePhone(value, "en-US");
const creditCard = (value) => isCreditCard(value);

export default {
  name: "CheckoutView",
  components: { CartViewButtons },
  data() {
    return {
      name: "",
      address: "",
      phoneNumber: "",
      email: "",
      ccNumber: "",
      ccExpiryMonth: new Date().getMonth() + 1,
      ccExpiryYear: new Date().getFullYear(),
      checkoutStatus: "",
    };
  },
  validations: {
    name: {
      required,
      minLength: minLength(4),
      maxLength: maxLength(45),
    },
    address: {
      required,
      minLength: minLength(4),
      maxLength: maxLength(45),
    },
    phoneNumber: {
      required,
      phone,
    },
    email: {
      required,
      email,
    },
    ccNumber: {
      required,
      creditCard,
    },
  },

  computed: {
    cart() {
      return this.$store.state.cart;
    },
    months() {
      return [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
    },
  },

  methods: {
    submitOrder() {
      console.log("Submit order");
      this.$v.$touch(); // Ensures validators always run
      if (this.$v.$invalid) {
        this.checkoutStatus = "ERROR";
      } else {
        this.checkoutStatus = "PENDING";
        setTimeout(() => {
          // this.checkoutStatus = "OK";
          setTimeout(() => {
            this.$store
              .dispatch("placeOrder", {
                name: this.name,
                address: this.address,
                phone: this.phoneNumber,
                email: this.email,
                ccNumber: this.ccNumber,
                ccExpiryMonth: this.ccExpiryMonth,
                ccExpiryYear: this.ccExpiryYear,
              })
              .then(() => {
                this.checkoutStatus = "OK";
                this.$router.push({ name: "confirmation-view" });
              })
              .catch((reason) => {
                this.checkoutStatus = "SERVER_ERROR";
                console.log("Error placing order", reason);
              });
          }, 1000);
        }, 1000);
      }
    },

    /* NOTE: For example yearFrom(0) == 2019 */
    yearFrom(index) {
      return new Date().getFullYear() + index;
    },
  },
};
</script>

