<template>
  <div>
    <div class="cart-table">
      <ul>
        <li class="table-heading">
          <div class="heading-book">Book</div>
          <!--        <div class="heading-title">Title</div>-->
          <div class="heading-price">Price / Quantity</div>
          <div class="heading-subtotal">Amount</div>
        </li>
        <li
          :key="item.book.bookId"
          v-for="item in $store.state.cart.items"
          class="table-content"
        >
          <div class="line-sep"></div>
          <div class="cart-book-image">
            <!--            <div class="rect square"></div>-->
            <img
              class="rect square"
              :src="
                require('@/assets/images/books/' + bookImageFileName(item.book))
              "
              :alt="item.book.title"
            />
          </div>
          <div class="cart-book-title">
            {{ item.book.title }}
          </div>
          <div class="cart-book-price">
            ${{ (item.book.price / 100).toFixed(2) }}
          </div>
          <div class="cart-book-quantity">
            <input
              type="number"
              :value="item.quantity"
              min="0"
              max="20"
              @change="(event) => updateCart(item.book, event.target.value)"
            />
          </div>
          <div class="cart-book-subtotal">
            ${{
              ((item.book.price / 100).toFixed(2) * item.quantity).toFixed(2)
            }}
          </div>
        </li>
      </ul>
    </div>
    <div class="cart-details-div">
      <div class="subtotal">
        <h3>
          <span class="price-span">Your Subtotal is:</span> ${{
            ($store.state.cart.subtotal / 100).toFixed(2)
          }}
        </h3>
      </div>
      <div class="total">
        <h3>
          <span class="price-span">Your Total is:</span> ${{
            ($store.state.cart.total / 100).toFixed(2)
          }}
        </h3>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CartTable",
  methods: {
    bookImageFileName: function (book) {
      let name = "" + book.bookId;
      name = name.toLowerCase();
      name = name.replace(/ /g, "-");
      name = name.replace(/'/g, "");
      console.log("Book image file name: " + name);
      return `${name}.gif`;
    },
    addToCart(book) {
      this.$store.dispatch("addToCart", book);
    },
    updateCart(book, quantity) {
      console.log("Book: " + book.title + "Quantity: " + quantity);
      this.$store.dispatch("updateCart", { book: book, quantity: quantity });
    },
  },
};
</script>

