<template>
  <li class="book-box">
    <div class="book-image">
      <a href="#">
        <img
          :alt="book.title"
          :src="require('@/assets/images/books/' + book.bookId + '.gif')"
        />
        <button class="read-now-btn" v-if="book.isPublic">Read Now</button>
      </a>
    </div>
    <div class="book-title">{{ book.title }}</div>
    <div class="book-author">{{ book.author }}</div>
    <div class="book-price">${{ (book.price / 100).toFixed(2) }}</div>
    <button class="button add-to-cart-btn" v-on:click="addToCart(book)">
      <i class="fa-solid fa-cart-plus"></i> Add to Cart
    </button>
  </li>
</template>

<script>
export default {
  name: "categoryBookListItem",
  props: {
    book: {
      type: Object,
      required: true,
    },
  },
  methods: {
    addToCart: function (book) {
      this.$store.dispatch("addToCart", book);
    },
  },
};
</script>
